/*Login Play Form*/
#login-play-form{
    position: absolute;
    top: 138px;
    right: 26px;
    width: 265px;
    height: 389px;
    background-color: #e5f4fa;
    border-radius: 10px;
    padding: 15px;
}
#login-play-form .login-payment-label{
    font-family: 'Fredoka One', cursive;
    color: #00528c;
    font-size: 18px;
    margin-bottom: 0;
}
#login-play-form .login-payment-input{
    font-size: 18px;
    border: solid #29a1cc 3px;
    border-radius: 10px;
    background-color: #e0eaf2;
    width: 100%;
    padding: 2px 10px;
}
form#login-play-form .remember-me-label{
    color: #00528c;
    font-size: 15px;
    font-weight: 700;
    margin-left: 10px;
    margin-top: 2px;
}
form#login-play-form .remember-me-label:hover{
    text-decoration: underline;
}
#login-play-form .remember-me-block{
    position: relative;
    margin-top: 20px;
    height: 30px;
}
#login-play-form .remember-me{
    display: block;
    position: absolute;
    top: -5px;
    left: 0;
    cursor: pointer;
}
#login-play-form .blank-tick{
    display: none;
    width: 22px;
    height: 22px;
}
#login-play-form .hidden-tick{
    display: none;
    position: absolute;
    top: 165px;
    left: 40px;
}
#login-play-form .remember-me-frame{
    border: solid steelblue 3px;
    border-radius: 5px;
    background-color: lightgrey;
    width: 30px;
    height: 30px;
    position: absolute;
    top: 0;
    left: 0;
}
#login-play-form a.forgotten-label{
    margin-left: 0;
    margin-top: -2px;
}
.logo-play{
    position: relative;
    top: 0;
    left: 0;
}
.nav-play{
    position: relative;
    top: 27px;
    left: 270px;
}
div.nav-icons-play{
    left: -15px;
}

