/** First passage to smaller size **/
@media only screen and (max-width: 990px) {
    .nav-bar .logo {
        display: none;
    }

    /* smaller header */
    .header{
        height:55px;
    }
    .toggle-menu-button {
        margin-top:9px;
    }
}

/** only white column visible */
@media only screen and (max-width: 770px) {
    /* Remove border radius when minimized */
    #container-content {
        border-radius: 0;
    }
}

/* fix for overlapping labels in forgot pass */
