/* Static Pages */
.container-content-pages{
    padding: 0 50px 40px 50px;
    text-align: justify;
}
.container-content-pages .answer-container{
    border: 2px solid lightgrey;
    border-radius: 5px;
    padding: 20px 40px;
    margin: 5px 0;
}
.container-content-pages p{
    margin-bottom: 20px;
}
.about-us-img{
    width: 100%;
    padding: 10px 50px;
}
.tech-img{
    width: 100%;
    padding: 0 60px;
}
.kid-certified{
    text-align: center;
    margin-top: 30px;
    margin-bottom: 40px;
}
.kid-certified-img{
    width: 150px;
    margin-bottom: 40px;
}
img[align="center"] {
    display: block;
    margin: 0 auto !important;
}
div.about-us-bottom{
    margin-top: 45px;
    padding: 0 90px;
}
.privacy-headings{
    margin-top: 25px;
}
.pages-green-headings{
    color: #72C02C;
    margin-bottom: 0;
}
.align-center{
    text-align: center;
}
.img-align-center{
    margin: 0 auto;
}
.big-heading{
    font-size: 32px;
    margin-bottom: 30px;
}
h1.big-heading-blue{
    border-radius: 5px;
    background-color: #34c2ff;
    padding: 12px;
    color: white;
    margin: 0;
    cursor: default;
    margin-bottom: 20px;
}
@media all and (max-width: 992px){
    h1.big-heading-blue{
        font-size: 26px;
    }
}
@media all and (max-width: 768px){
    h1.big-heading-blue{
        font-size: 20px;
    }
}
@media all and (max-width: 768px){
    h1.selected-package{
        font-size: 14px;
    }
}
.middle-heading{
    font-size: 24px;
}
.questions-answers h3{
    margin-bottom: 10px;
}
.question-links a{
    color: #555;
}
.question-links a:hover{
    text-decoration: none;
    color: #72C02C;
}
.back-to-top {
    cursor: pointer;
}
.back-to-top:hover{
    text-decoration: none;
}
.mb15{
    margin-bottom: 15px;
}
form.contact-us-form{
    font-family: 'McLaren', cursive;
    font-size: 19px;
    border: none;
}
.contact-us-form .select select,
.contact-us-form .input input,
.contact-us-form .checkbox,
.contact-us-form .textarea textarea{
    font-family: 'McLaren', cursive;
    font-size: 17px;
    font-weight: lighter;
    color: grey;
}
#contact_us textarea{
    max-width: 640px;
}
select.contact-us-select{
    padding: 0;
}
#main_questions,
#billing,
#unsubscribe,
#tech,
#bad_behaviour,
#payment_email_section,
#game_card_pin_section,
#send_contact_us_form{
    display: none;
}
#contact_us{
    max-width: 700px;
    margin: auto;
}
.error-text{
    font-size: 18px;
    font-weight: bold;
    color: red;
}
.success-text{
    font-size: 18px;
    font-weight: bold;
    color: #72C02C;
}
.tycoon-img img{
    margin: 0 auto 25px;
    display: block;
    max-width: 100%;
    height: auto;
}
.membership-parents img{
    display: block;
    max-width: 100%;
    height: auto;
}
.video-container {
    position: relative;
    padding-bottom: 56.25%;
    padding-top: 0;
    margin-bottom: 50px;
    height: 0;
    overflow: hidden;
}
.video-container iframe, .video-container object, .video-container embed {
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
}
.under-membership{
    font-weight: bold;
    margin-top: 10px;
    cursor: default;
}
.under-membership p{
    margin-bottom: 0;
}
div.payment-summary-block{
    background-color: #e5f4fa;
    border-radius: 5px;
    font-family: "Arial", serif;
    margin-top: 20px;
    padding: 20px;
}
div.payment-summary-block p{
    color: black;
    margin: 6px 0 20px;
}
@media all and (min-width: 992px){
    .things-img{
        margin-top: 17px;
    }
}
@media all and (max-width: 992px){
    .things-img{
        margin-top: 9px;
    }
}
@media all and (min-width: 992px){
    .main-row{
        padding-left: 20px;
        padding-right: 20px;
    }
}
@media all and (max-width: 992px){
    .main-row{
        padding-left: 15px;
        padding-right: 15px;
    }
}
@media all and (min-width: 992px){
    .label-container-pass{
        margin-top: 15px;
    }
}
@media all and (max-width: 992px){
    #login-payment-form{
        min-height: 548px;
    }
}
@media all and (min-width: 992px){
    #login-payment-form{
        max-width: 396px;
    }
}
#login-payment-form{
    background-color: #e5f4fa;
    border-radius: 10px;
    padding: 15px;
    margin: 0 auto;
}
#login-payment-form .login-payment-label{
    font-family: 'Fredoka One', cursive;
    color: #00528c;
    font-size: 24px;
    margin-bottom: 0;
}
#login-payment-form .login-payment-input{
    font-size: 18px;
    border: solid #29a1cc 3px;
    border-radius: 10px;
    background-color: #e0eaf2;
    width: 100%;
    padding: 5px 15px;
}
form#login-payment-form .remember-me-label{
    color: #00528c;
    font-size: 18px;
    font-weight: 700;
    margin-left: 10px;
}
form#login-payment-form .remember-me-label:hover{
    text-decoration: underline;
}
#login-payment-form .remember-me-block{
    position: relative;
    margin-top: 20px;
    height: 40px;
}
#login-payment-form .remember-me{
    display: block;
    position: absolute;
    top: 2px;
    left: 10px;
    cursor: pointer;
}
#login-payment-form .blank-tick{
    display: none;
    width: 22px;
    height: 22px;
}
#login-payment-form .hidden-tick{
    display: none;
    position: absolute;
    top: 165px;
    left: 40px;
}
#login-payment-form .remember-me-frame{
    border: solid steelblue 3px;
    border-radius: 5px;
    background-color: lightgrey;
    width: 30px;
    height: 30px;
    position: absolute;
    top: 0;
    left: 0;
}
.grey-container{
    background-color: #f2f2f2;
    border-radius: 10px;
    min-height: 180px;
    padding: 6px 12px;
    margin-top: 30px;
}
.grey-container p{
    padding-left: 10px;
    cursor: default;
    margin: 0;
}
.heading-blue{
    border-radius: 10px;
    background-color: #34c2ff;
    padding: 6px 10px;
    color: white;
    cursor: default;
    margin: 0;
}
.blue-text{
    color: #34c2ff;
}
.blue-text:hover{
    color: #34c2ff;
    text-decoration: none;
}
.relative{
    position: relative;
}
.grey-border-top{
    border-top: solid 4px darkgrey;
}
div.for-letters{
    max-width: 500px;
}
div.for-letters p{
    color: black;
    text-align: justify;
}
#game-card-form #pin,
#game-card-form #email{
    width: 100%;
    margin-top: 10px;
}
#game-card-form #agree{
    margin-top: 20px;
}
.membership-cards-title{
    margin-bottom: 30px;
    margin-top: 20px;
}
.star{
    position: absolute;
    z-index: 1;
}
.star-lg{
    top: -30px;
    right: -70px;
}
.star-md{
    top: -36px;
    right: -70px;
}
.star-sm{
    top: -28px;
    right: -53px;
}
.nav-arrow-membership{
    left: 95px;
}
.nav-arrow-parents{
    left: 400px;
}
.nav-arrow-help{
    left: 535px;
}
.retailers-block{
    background-color: lightgrey;
    border-radius: 30px;
    margin-bottom: 50px;
}
#loading{
    display: none;
    margin-top: 15px;
}
.paragraph{
    margin-bottom: 30px;
}
div.paragraph p{
    margin: 0;
}
.nav-right-play{
    position: absolute;
    top: 16px;
    right: 25px;
}
.questions-titles{
    margin: 40px 0 10px;
}
